// @import "foundation/settings";
@import "fsettings";
@import "motion-ui";
@import "foundation";
@import "colors";


@include foundation-everything($flex: true);

html {
    background-color: black;
}
// Body
@keyframes body {
    0% { opacity: 0; }
    30% { opacity: 0; }
    100% { opacity: 1; }
}
body {
    animation: body 1s linear normal;
    @include breakpoint(small up) {
        // background-color: black;
        background-image: url(../imgs/mockup1_bckg2.jpg);
        background-size: cover;
        background-color: rgba(255,216,201,1);
        background-blend-mode: multiply;
    };
    @include breakpoint(small down) {
        // background-color: black;
        background-image: url(../imgs/mockup1_bckg2_mini.jpg);
        background-size: cover;
    };
    // background-image:  url(../imgs/mockup1_bckg1_light.jpg);
    // background-image:  url(../imgs/mockup1_bckg2.jpg);
    // background-size: cover;
    // background-color: rgba(255,216,201,1);
    // background-blend-mode: multiply;
    position: relative;
    &:before {
        position: absolute;
        content: "";
        background-image: radial-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.4));
        height: 100%;
        width: 100%;
        pointer-events: none;
    }
}
// Links

// Icons
@mixin icon-mini {
    // transform: scale(1);
    // transition: 0.5s;
    //
}
.icon-facebook-squared {
    font-size: 95%;
    // color: lighten(blue, 40%);
    $fb_color: #6996BD;
    color: $fb_color;
    &:hover {
        color: lighten($fb_color, 10%);
    }
}
.icon-phone {
    font-size: 80%;
}
// Logo
.upper-block {
    height: 8rem;
    width: 100%;
    margin: 0 auto;
    position: absolute;
    top: 0;
    z-index: 100;
    // opacity: 0.7;
    background-image: linear-gradient(to bottom, rgba(black, 0.8), rgba(black, 0.05));
    background-image: radial-gradient(50% 50%, contain, black, black 20%, rgba(black, 0));
    text-align: center;
}
.bottom-block {
    height: 6rem;
    width: 100%;
    margin: 0 auto;
    position: absolute;
    bottom: 0;
    z-index: 100;
    // background-image: linear-gradient(to top, rgba(black, 1),  rgba(black, 1) 40%, rgba(black, 0.05) 80%, rgba(black, 0.05));
    background-image: radial-gradient(50% 50%, contain, black, black 20%, rgba(black, 0));
    // @include breakpoint(large up) {
    //     background-image: radial-gradient(50% 50%, contain, black, black 20%, rgba(black, 0));
    // };
    padding-top: 1rem;
    @include breakpoint(medium down) {
        background: rgba(black, 0.8);
        padding-top: 1rem;
        height: 5rem;
    };
    // background-image: radial-gradient(50% 50%, contain, black, black 20%, rgba(black, 0));
    text-align: center;
    color: #C0C4CC;
    h3 {
        margin-bottom: 0;
    }
    a {
        $color: lighten(desaturate(blue, 90%), 20%);
        color: $color;
        transition: 0.5s;
        // transform: scaleY(1.0) translateY(1%);
        &:hover {
            transform: scaleY(1.1);
            transition: 0.2s;
            color: darken($color, 10%);
        }
    }
    .small {
        color: dark-gray;
    }
}
.logo-item {
    width: 14rem;
    margin-top: .5rem;
    opacity: 0.8;
    // background-image: radial-gradient(rgba(black, 1), rgba(black, 0));
    // height: 20rem;
}

.athlete {
    background-image: url("../imgs/athlete.png");
    background-size: cover;
    height: 100%;
    width: 45%;
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 100;
    pointer-events: none;
}

// Main
.container {
    @include breakpoint(xlarge up) { @include flex-grid-row($width: 100rem, $columns: 5); }
    @include breakpoint(xlarge down) { @include flex-grid-row($width: 100rem, $columns: 6); }
    height: 100%;
    margin: 0 auto;
}
@keyframes navitem-flash {
    0% {
        opacity: 0.6
    }
    20% {
        opacity: 0.7;
    }
    60% {
        opacity: 0.5;
    }
    100% {
        opacity: 0.8;
    }
}

// Sidebar, Menu
@keyframes smoke {
    to {
        background-position-y: 0px;
    }
    to {
        background-position-y: -256px;
    }
}
.sidebar {
    // @include breakpoint(xlarge up) {
    //     @include flex-grid-column(1 of 5, $gutter: 0.02rem);
    // };


    @include breakpoint(xxlarge up) {
        @include flex-grid-column(1 of 5, $gutter: 0.02rem);
        // @include flex-grid-row(100%, $gutter: 0.01rem);
        height: 100%;
        // display: flex;
    };
    @include breakpoint(xlarge down) {
        @include flex-grid-column(2 of 6, $gutter: 0.02rem);
        // @include flex-grid-row(100%, $gutter: 0.01rem);
        height: 100%;
        // display: flex;
    };
    @include breakpoint(medium down) {
        // @include flex-grid-column(2 of 6, $gutter: 0.02rem);
        // height: 100%;
        // position: absolute;
        // top: 0;
        // left: 0;
        // height: 100%;
        // width: 25rem;
        // background: rgba(black, 0.8);
        display: none;
    };
    // @include breakpoint(small down) {
    //     display: none;
    // };

    // @include breakpoint(medium down) { width: 20rem; }

    @include flex-grid-row(100%, $gutter: 0.01rem);
    @include flex-align(right, middle);

    padding-right: 0;
    position: relative;
    // background: rgba(255,255,255,0.1);
    background-image: linear-gradient(to right, rgba(black, 0), rgba(white, 0.1));
    border-right: 5px solid rgba(red, 0.3);
    @include breakpoint(medium up) {
        &:before {
            content: "";
            position: absolute;;
            left: 30%;
            top: 0;
            height: 100%;
            width: 90%;
            background-image: url("../imgs/Fog05.png");
            background-size: 100% 256px;
            opacity: 0.8;
            // animation: smoke 10s linear infinite;
            pointer-events: none;
            // z-index: -100;
        }
    }
    nav {
        @include flex-align(right, middle);
        color: white;
        @include breakpoint(large up) {font-size: 1.8rem;};
        @include breakpoint(large down) {font-size: 1.5rem; line-height: 2rem;};
        ul {
            @include menu-base();
            @include menu-direction(vertical);
            li {
                text-align: right;
                &.active {
                    background-image: linear-gradient(to right, rgba(black,0), rgba(red, 0.1));
                }

                a {
                    transform: scale(1);
                    transition: 0.5s;
                    &:hover {
                        transform: scale(1.1) translateX(-3%);
                        transition: 0.3s;
                        animation: navitem-flash 600ms linear infinite;
                    }
                }
            }
        }
    }
}


.menu-toggler {
    height: 10rem;
    width: 5rem;
    // background-color: yellow;
    // position: absolute;
    left: 1rem;
    top: 5rem;
    height: 5rem;
    z-index: 1000;
    @include breakpoint(large up) {
        display: none;
    };
}

#mobileSidebar {
    // @include breakpoint(large up) {
    //     display: none;
    // }
    .close-menu {
        width: 10rem;
        height: 10rem;
        background-color: rgba(lighten(black, 10%), 1);
        color: desaturate(gray, 80%);
        border: 1px dotted desaturate(gray, 80%);
        position: absolute;;
        left: 2rem;
        top: 2rem;
        // text-align: center;
        font-size: 150%;
        @include flex-grid-row;
        flex-direction: column;
        @include flex-align(center, middle);
        border-radius: 10px;
    }
    position: absolute;
    left: -1rem;
    top: 0;
    // height: 90%;
    // width:40rem;
    width: 90%;
    @include flex-grid-row();
    @include flex-grid-column();
    display: none;;
    @include breakpoint(large up) {
        display: none;
    }
    @include flex-align(center, middle);
    height: 100%;
    nav {
        @include flex-align(center, middle);
        color: white;
        background-color: rgba(black, 0.9);
        font-size: 1.6rem;
        // line-height: 1.8rem;
        height: 90%;
        ul {
            @include menu-base();
            @include menu-direction(vertical);
            height: 100%;
            li {
                text-align: right;;
                // a {color: darken(white, 30%);}
                &.active {
                    background-image: linear-gradient(to right, rgba(black,0), rgba(red, 0.1));
                }

                // a {
                //     transform: scale(1);
                //     transition: 0.5s;
                //     &:hover {
                //         transform: scale(1.1) translateX(-3%);
                //         transition: 0.3s;
                //         animation: navitem-flash 600ms linear infinite;
                //     }
                // }
            }
        }
    }
}

@keyframes video-hue {
    from {
        filter: hue-rotate(180deg);
    }
    to {
        filter: hue-rotate(50deg);
    }
}
.content {
    @include breakpoint(xlarge down) { @include flex-grid-column(4 of 6); };
    @include breakpoint(medium down) { 
        @include flex-grid-column(5.5 of 6); 
        /*@include grid-column-offset(0.1);*/
        // font-size: rem-calc(2.3rem);
        }
    @include breakpoint(xxlarge up) { @include flex-grid-column(3 of 5); };
    @include flex-grid-row();
    @include flex-grid-column();
    @include flex-align(left, middle);
    height: 100%;
    overflow: hidden;
    .textblock {
        // @include flex-grid-row($behavior: collapse );
        // h1 {
        //     @include flex-grid-column(1 of 1, $gutter: 0);
        // }
        // @include flex-grid-column();
        @include breakpoint(large up){
            p { margin-right: 50px !important;}
        };
        @include breakpoint(xlarge up){
            // min-width: 64rem;
        }
        @include breakpoint(large down){
            // TODO: Width of textblock
            // p, h4 {
            //     width: 30rem;
            // }
            width: 61rem;
        }
        overflow-y: scroll;
        height: 90%;
        width: 100%;
        padding: 5rem 60px 1rem 50px;;
        position: relative;
        right: -2rem;
        margin-right: -40px;
        padding-left: 0;
        // background-color: rgba(black, 0.2);
        background-image: linear-gradient(to right, rgba(black, 0.0), rgba(black, 0.4) 5%, rgba(black, 0.4) 90%, rgba(black, 0));
        color: $medium-gray;
        // color: $black;;
        .saleblock {
            @include flex-grid-row;
            @include flex-align(spaced, middle);
            @keyframes sale-img{
                0% { transform: scale(0.9); }
                80% { transform: scale(0.9); }
                90% { transform: scale(1.1); }
                100% { transform: scale(1); }
            }
            // @keyframes saleimg-flick {
            //     from {opacity: 0.9}
            //     to {opacity: 1}
            // }
            .saleimg {
                animation: sale-img 5s ease-in-out;
                // animation: saleimg-flick 5s ease-in-out infinite alternate;
                transition: 0.5s;
                &:hover {
                    transform: scale(1.1);
                    transition: 0.3s;
                }
            }
        }
        .fitness-video {
            @include breakpoint(medium down) {
                // display: none;
                margin-top: 3rem;
                // height: 50%;
                // width: 75%;
                
                // background-size: 100% 100%;
                // background-repeat: no-repeat;
                // margin-bottom: 20rem;
                padding-bottom: 0;
            }
            width: 93%;
            margin: -10% auto;

            // mask: url(../imgs.mask.svg) top left / cover;
            // filter: hue-rotate(180deg);
            // animation: video-hue 8s ease-in-out infinite alternate;
        }
        .vcon {
            position: relative;
            text-align: center;
            margin-bottom: 0.2rem;
            top: -5rem;

            .video-grid {
                pointer-events: none;
                position: relative;;
                content: "";
                top: 0;
                left: 0;
                width: 100%;
                height: 120%;
                margin: 0 auto;
                // @include breakpoint(medium down) {
                //     position: relative;
                //     width: 100%;
                //     height: 50%;
                //     // top: 80%;
                //     background-image: url(../imgs/novideo.jpg);
                //     background-repeat: no-repeat;
                // }
                // -webkit-mask-box-image: url(../imgs/mask.svg);
                // mask: url(../imgs/mask.svg) top left / cover;
                mask: url(../imgs/mask.svg) 50% 90%/100% no-repeat;
                &:before {
                    pointer-events: none;
                    position: absolute;
                    content: "";
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-image: url("../imgs/black-criss-cross.png");
                    background-image: url("../imgs/black-zig-zag.png");
                    background-image: url("../imgs/black-horizontal-lines.png");
                }
                // background-size: 100% 100%;
                // background-repeat: no-repeat;
            }
        }
    }
}



@include breakpoint(xxlarge up) {
    .empty-column {
        @include flex-grid-column(1 of 5);
