  font-family: 'iconfontmini';
  src: url('../font/iconfontmini.eot?37442996');
  src: url('../font/iconfontmini.eot?37442996#iefix') format('embedded-opentype'),
       url('../font/iconfontmini.woff?37442996') format('woff'),
       url('../font/iconfontmini.ttf?37442996') format('truetype'),
       url('../font/iconfontmini.svg?37442996#iconfontmini') format('svg');
  font-weight: normal;
  font-style: normal;
}
